import React from 'react';
import 'antd-mobile/dist/antd-mobile.css'; 
import{Button,TabBar} from 'antd-mobile';
import {withRouter} from 'react-router-dom';


class Mytab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    //   selectedTab: 'blueTab',
      hidden:false,
      fullScreen: false,
    };
  }

  render() {
    const {pathname}=this.props.location;
    return (        
      <div style={{   position:'fixed',bottom:'0',width: '100%'}}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#33A3F4"
          //可以改下面图标的背景色
		      barTintColor="white"
        >
          <TabBar.Item
            title="首页"
            key="首页"
            icon={
                <i className='iconfont icon-Homehomepagemenu'></i>
            }
            selectedIcon={
                <i className='iconfont icon-Homehomepagemenu1' ></i>
            }
			      selected={pathname === '/home'}
			      //显示信息1条
            // badge={1}
            onPress={() => {
              this.props.history.push('/home');
            }}
            data-seed="logId"
          >
          </TabBar.Item>
          <TabBar.Item
            icon={
                <i className='iconfont icon-shoucang'></i>
            }
            selectedIcon={
                <i className='iconfont icon-tubiao14'></i>
            }
            title="收藏"
            key="收藏"
            // badge={'new'}显示信息new红标
            selected={pathname === '/shoucang'}
            onPress={() => {
              this.props.history.push('/login');
              // this.props.history.push('/shoucang')
            }}
            
            data-seed="logId1"
          >
          </TabBar.Item>
          <TabBar.Item
            icon={
                <i className='iconfont icon-gouwuche'></i>
            }
            selectedIcon={
                <i className='iconfont icon-gouwuche1'></i>
            }
            title="购物车"
            key="购物车"
            // badge={'new'}显示信息new红标
            selected={pathname === '/cart'}
            onPress={() => {
              this.props.history.push('/cart')
            }}
            data-seed="logId1"
          >
          </TabBar.Item>

          <TabBar.Item
            icon={
                <i className='iconfont icon-dingdan'></i>
            }
            selectedIcon={
                <i className='iconfont icon-dingdan1'></i>
            }
            title="订单列表"
            key="订单列表"
            // dot
            selected={pathname === '/list'}
            onPress={() => {
                this.props.history.push('/list')
            }}
          >
          </TabBar.Item>

		  <TabBar.Item
            icon={
                <i className='iconfont icon-wode'></i>
            }
            selectedIcon={
                <i className='iconfont icon-wodedangxuan'></i>
            }
            title="我的淘宝"
            key="我的淘宝"
            selected={pathname === '/my'}
            onPress={() => {
                this.props.history.push('/my')
            }}
          >
          </TabBar.Item>

        </TabBar>
      </div>
    );
  }
}


export default withRouter(Mytab);
