import React from 'react';
import {connect} from 'react-redux';

const Input = (props) => {
    console.log(props);
    return (
        <div>
            <input 
                type="text" 
                onKeyDown={(e)=>{
                    if(e.keyCode===13){
                        props.dispatch({
                        type:"ADD_TODO",
                        value:e.target.value
                        })
                    }
                }}
            />
        </div>
    )
}  

export default connect()(Input);
