import React from 'react'
import {connect} from 'react-redux'
import {login} from './actionCreator'
// import Login0 from './Login0'

const Login = (props) => {
    const log=()=>{
        props.dispatch(login());
    }
    console.log(props);
    return <div >
                {props.userinfo
                    ?<div>
                        {props.history.push('/shoucang')}
                    </div>
                    :
                    <div style={{background:'blue',width:'375px',height:'667px'}}>
                        <form>
                            <input type="text" name="name" style={{margin:'250px 55px 30px',width:'250px',height:'35px'}} placeholder="用户名"></input>
                            <input type="password" name="password" style={{marginLeft:'55px',width:'250px',height:'35px'}} placeholder="密码"></input>
                        </form>
                        <button style={{marginLeft:'130px',marginTop:'40px',width:'100px',height:'35px'}}
                                onClick={log} 
                            >登录</button>
                    </div>
                }
            </div>
}

const mapStateToProps=(state)=>{
    console.log(state.login.userinfo);
    return {userinfo:state.login.userinfo};
}

export default connect(mapStateToProps)(Login)