import React from 'react'
import {connect} from 'react-redux'
import Mytab from './Mytab'

const Shoucang = (props) => {
    return (
        <div>
            <h1 style={{textAlign:'center'}}>收藏列表</h1>
            {
                props.mydata.map((item,idx)=>
                (<div key={idx} style={{margin:'auto'}}>
                    <img src={item.img} style={{width:'200px',height:'250px'}}></img>
                    <li style={{width:'200px'}}>{item.title}<button onClick={(e)=>props.dispatch({
                        type:"DELETE",
                        index:idx
                    })}>删除</button></li>
                </div>
                ))
            } 
            <div style={{height:'50px',width:'100%'}}></div>
            <Mytab/>
        </div>
    )
}
const mapStateToProps=(state)=>{
    return{
        mydata:state.shoucang
    }
}

export default connect(mapStateToProps)(Shoucang);