import React, { Component, useEffect } from 'react';
import { Icon } from 'antd-mobile';
import './Details.css';
import {connect} from 'react-redux';

const Details=(props)=>{
        {
                useEffect(()=>{
                        var sc=document.getElementById('sc');
                        props.mydata.map(item=>{
                                if(props.location.state[0]===item.img){
                                        sc.className="iconfont icon-shoucang1";
                                }
                        })
                })
        }
        return (<div className="details">
                        <img src={props.location.state[0]}></img>
                                <Icon type="left" size="lg"  style={{position:'relative',top:'-400px',opacity:'0.5'}} onClick={()=>props.history.push('/home')}/>
                                <div className="bot">
                                        <ul>
                                                <li className='iconfont icon-dianpu' ><div id="store">店铺</div></li>
                                                <li className='iconfont icon-kefuxiaoxixinxi' ><div id="service">客服</div></li>
                                                <li className='iconfont icon-shoucang ' id="sc" 
                                                        
                                                        onClick={()=>{
                                                                if(props.userinfo===''){
                                                                        props.history.push('/login');
                                                                }else{
                                                                        var sc=document.getElementById("sc");
                                                                        sc.className="iconfont icon-shoucang1";
                                                                        props.dispatch({type:'COLLECT',value:{img:props.location.state[0],title:props.location.state[2]}})
                                                                }
                                                        }}>
                                                        <div id="collect">收藏</div>
                                                </li>
                                                <li id="bright">
                                                        <span id="bright1" >加入购物车</span>
                                                        <span id="bright2" >立即购买</span>
                                                </li>
                                        </ul>
                                </div> 
                </div>
        )
}
const mapStateToProps=(state)=>{
        console.log(state);
        return{
                userinfo:state.login.userinfo,
                mydata:state.shoucang
        }
    }
    
export default connect(mapStateToProps)(Details);
