import React, { Component } from 'react'
import Imgs from './Imgs'
import Shops from './Shops'
import Details from './Details'
import Login from './Login'
import Mytab from './Mytab'
import './Home.css'

const Home=(props)=> {
        
        return <div className="home">
            <div className="top0">
                <div className="search0" onClick={()=>props.history.push('/search')}>
                    <p>淘</p>
                    <div className="tright">                  
                        <span className="iconfont icon-wode" >  寻找宝贝店铺</span>
                    </div>
                </div>   
                <div className="imgs">
                    <Imgs/>
                </div>
                <div className="shops">
                    <Shops data={props}/>
                </div>
                <div className="bottom">
                    
                </div> 
                
                <Mytab/>
            </div>
           
            </div>
}
export default Home;
