import { WhiteSpace } from 'antd-mobile'
import React, { Component } from 'react'
import {connect} from 'react-redux'
import {datalist} from './actionCreator'
import store from'./Store';
import {Provider} from 'react-redux';


const Shops=(props)=>{
    console.log(11111,props.datalist);
    const ls=()=>{
        props.dispatch(datalist());
    }
    return <div onLoad={ls()}>
                <ul >
                { props.datalist.map((item,index)=>
                        <li key={index} style={{width:'160px',height:'220px',background:'white',float:'left',margin:'10px 10px',borderRadius:'10px'}}>
                            <img src={`${item.img}`} style={{width:'160px',height:'160px',borderRadius:'10px'}} onClick={()=>{props.data.history.push('/details',[item.img,item.id,item.title]); console.log(item.img)}}>
                            </img>
                            <p style={{fontSize:'14px',fontFamily:'宋体'}} >
                                {item.title}
                            </p>
                            <p style={{fontSize:'12px',paddingBottom:'10px'}}>
                                <span style={{fontSize:'15px',color:'red',fontWeight:'bold'}}>
                                    ￥{item.price}
                                </span>
                            </p>
                        </li>
                    
                )}
                </ul>
            </div>
}
//find（找到）、include（含有）、map（循环）、filter过滤
const mapStateToProps=(state)=>{
    console.log(555555,state);
    return {datalist:state.datalist.datalist}
};

export default connect(mapStateToProps)(Shops)