import React, { Component } from 'react'
import { Carousel } from 'antd-mobile'
import './Home.css'

export default class Home1 extends Component {
    state = {
        data: [1,2,3],
        imgHeight: 176,
      }
      componentDidMount() {
        // simulate img loading
        setTimeout(() => {
          this.setState({
            data: [
              'https://picasso.alicdn.com/imgextra/i3/382208/O1CNA1iyU1IP100ff4af62860b14de730004_!!382208-0-picassobanner.jpg', 
              'https://img.alicdn.com/imgextra/i2/96/O1CN014g0ADB1Ca0eWkU60Z_!!96-0-luban.jpg',
               'https://img.alicdn.com/imgextra/i3/27/O1CN019CAYMI1C4PK6mp9qX_!!27-0-luban.jpg'
              ],
          });
        }, 100);
      }
    render() {
        return ( 
                <Carousel autoplay infinite>
                    {this.state.data.map(val => (
                        <a key={val} href="http://www.alipay.com"
                        style={{ display: 'inline-block',  height: this.state.imgHeight}}
                        >
                            <img src={`${val}`}
                            alt="" style={{ width:'100%', height:'176px',verticalAlign: 'top' }}
                                onLoad={() => {
                                    window.dispatchEvent(new Event('resize'));
                                    this.setState({ imgHeight: 'auto' });
                                }}
                            />
                        </a>
                    ))}
                </Carousel>   
        )  
    }
}
